body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg{
    background-image: -webkit-linear-gradient(left, #ee6e73, #ee6eb4);
    background-image: linear-gradient(to right, #ee6e73, #ee6eb4);
    color: white;
}

.exit {
    font-size: 1.8rem;
    padding-top: .8rem;
}
.pink {
    background-image: -webkit-linear-gradient(left, #ec6a00, #dd4620);
    background-image: linear-gradient(to right, #ec6a00, #dd4620);
    color: white;
    padding: 1rem;
    width: 100%;
    margin-top: 1rem;
}

.social {
    padding: 1rem;
    width: 100%;
    margin-top: 1rem;
}

.inputs {
    height: 4rem;
}

.logo{
    height: 60vmin;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
}

.bee{
    margin-left: 2rem;
}

